export default {
	data() {
		return {
			dynamic1: true,
			dynamic2: false,
			dynamic3: false,
			type: "a",
			dynamicImgChange: "dynamicImg1",
			room: [],
			floor: [],
			floorIndex: 0,
			floorId: {},
			newFloorId: '',
			num: [],
			numIndex: 0,
			numId: {},
			newNumId: '',
			ImgUrl: this.ImgUrl
		}
	},
	methods: {
		//切换内容
		tapInformation(e) {
			if (e == 1) {
				this.dynamic1 = true;
				this.dynamic2 = false;
				this.dynamic3 = false;
				this.type = "a";
				this.dynamicImgChange = "dynamicImg1"
			} else if (e == 2) {
				this.dynamic1 = false;
				this.dynamic2 = true;
				this.dynamic3 = false;
				this.type = "b";
				this.dynamicImgChange = "dynamicImg2"
			}
			else if (e == 3) {
				this.dynamic1 = false;
				this.dynamic2 = false;
				this.dynamic3 = true;
				this.type = "c";
				this.dynamicImgChange = "dynamicImg3"
			}
		},
		
		// 获取会议室
		getMeet() {
			let baseUrl = this.WebUrl
			let that = this
			this.room = []
			uni.getStorage({
				key: "area_id",
				success:(res) => {
					console.log(res.data)
					uni.request({
						url: baseUrl + '/getData',
						data: {
							area_id: res.data,
							floor_id: that.newFloorId || '',
							num_id: that.newNumId || ''
						},
						success(res) {
							console.log(res)
							console.log('会议室')
							if(res.data.msg === '请求成功!') {
								res.data.data.forEach(function(item,index){
									that.room.push(item)
									console.log(item)
									that.room[index].file_url = that.ImgUrl + item.file_url[0]
								})
							}
						}
					})
				}
			})
		},
		
		// 获取楼层
		getFloor() {
			let baseUrl = this.WebUrl
			let that = this
			this.floor = ["区域"]
			uni.getStorage({
				key: "area_id",
				success:(res) => {
					console.log(res.data)
					uni.request({
						url: baseUrl + '/getFloor',
						data: {
							area_id: res.data
						},
						success(res) {
							console.log(res)
							if(res.data.msg === '请求成功!') {
								res.data.data.forEach(function(item){
									let name = item.dict_name
									that.floor.push(name + "楼")
									that.floorId[name + "楼"] = {id: item.dict_id}
								})
							}
						}
					})
				}
			})
		},
		
		// 获取人数
		getNum() {
			let baseUrl = this.WebUrl
			let that = this
			this.num = ["人数"]
			uni.getStorage({
				key: "area_id",
				success:(res) => {
					console.log(res.data)
					uni.request({
						url: baseUrl + '/getNum',
						data: {
							area_id: res.data
						},
						success(res) {
							console.log(res)
							console.log(33333)
							if(res.data.msg === '请求成功!') {
								res.data.data.forEach(function(item){
									let num = item.dict_name
									that.num.push(num + "人")
									that.numId[num + "人"] = {id: item.dict_id}
								})
							}
						}
					})
				}
			})
		},
		
		// 跳转到会议室
		toRoom(e) {
			let meet_name = e.meet_name
			uni.navigateTo({
				url: '../ranch/room?meet_name=' + meet_name
			});
		},
		
		// 切换楼层
		floorChange(e) {
			this.floorIndex = e.target.value
			let newFloorId = ''
			for(let key in this.floorId){
				if(key === this.floor[this.floorIndex]) {
					newFloorId = this.floorId[key].id
				}
			}
			this.newFloorId = newFloorId
			this.getMeet()
		},
		
		// 切换人数
		numChange(e) {
			this.numIndex = e.target.value
			let newNumId = ''
			for(let key in this.numId){
				if(key === this.num[this.numIndex]) {
					newNumId = this.numId[key].id
				}
			}
			this.newNumId = newNumId
			this.getMeet()
		},
		
		// 扫码
		scanCode() {
			wx.scanCode({
				onlyFromCamera: true,
				success (res) {
					console.log(res)
				},
				fail (err) {
					console.log(err)
				}
			})
		}
	},
	onShow() {
		this.getMeet()
		
		this.getFloor()
		
		this.getNum()
	}
}
